@media screen and (min-width: 320px) {
  .SignupPageWrapper {
    position: block;
    height: 100vh;
    width: 320px;
    background-image: url('/public/images_auth/Bottle_SignIn_mobile.svg'), url('/public/images_auth/Background_SignIn_mobile.svg');      
    background-color: var(--primery-color-white);
    background-repeat: no-repeat;
    background-size: contain; 
    background-position: center bottom;
    margin: 0 auto;
  }
}
@media screen and (min-width: 768px) {
  .SignupPageWrapper {
    width: 768px;
    background-image: url('/public/images_auth/Bottle_SignIn_tablet.svg');
  }
}
@media screen and (min-width: 1440px) {
  .SignupPageWrapper {
    width: 1440px;
    background-image: url('/public/images_auth/Bottle_SignIn_desktop.svg'), url('/public/images_auth/Background_MainPage.svg');
    background-position: left top, center top;
    height: 680px;
  }
}